hr {
 width: 48px; }
.section {
 background-color: #006a00; }

.main {
 text-align: center;
 padding-top: 110px;
 &__title {
  color: #ffffff;
  font-family: "Proxima Nova";
  font-size: 36px;
  font-weight: 700; }
 &__subtitle {
  color: #ffffff;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-weight: 400;
  margin-top: 49px; }
 &__block {
  margin-top: 90px;
  width: 360px;
  height: 660px;
  box-shadow: 0 14px 21px rgba(0, 0, 0, 0.17);
  background-color: #ffffff;
  margin-right: 20px; }
 &__text {
  color: #1a1820;
  padding-top: 50px;
  font-family: "Proxima Nova";
  font-size: 19px;
  font-weight: 700;
  line-height: 20px;
  padding-bottom: 33px; }
 &__img {
 	margin-bottom: 37px; }
 &__price {
  color: #1a1820;
  font-family: "Proxima Nova";
  font-size: 19px;
  font-weight: 700;
  line-height: 20px;
  font-size: 30px;
  margin-bottom: 32px; }
 &__text-2 {
  color: #1a1820;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-weight: 400;
  line-height: 30px; }
 &__button-1 {
  margin-top: 112px;
  color: #ffffff;
  font-family: "Proxima Nova - Semibold";
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  box-shadow: 0 8px 13px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  border: 1px solid #ffba15;
  background-color: #ffb504;
  background-image: linear-gradient(to top, #f5ad00 0%, #ffb504 100%);
  width: 209px;
  height: 65px; }
 &__button-2 {
  margin-top: 55px;
