.footer {
 text-align: center;
 font-family: "Proxima Nova";
 &__title {
  color: #fcfcfc;
  font-size: 36px;
  font-weight: 700;
  line-height: 18px;
  margin-bottom: 59px;
  padding-top: 110px; }
 &__subtitle {
  color: #fcfcfc;
  font-size: 19px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 93px; }
 &__block {
  padding-right: 75px; }
 &__button {
  margin-top: 70px;
  color: #ffffff;
